<!--
 * @Author: Marlon
 * @Date: 2020-02-21 08:28:50
 * @LastEditors: Marlon
 * @LastEditTime: 2020-02-21 09:25:57
 * @Description: 非菜单页面
 -->
<template>
  <el-container class="layout">
    <el-header class="header">
      <div class="logo">
        <div class="logo-big">logo</div>
        <div class="logo-title">GDM</div>
      </div>
      <div class="menu">
        <div class="icon-menu">——</div>
        <ul class="menu-list">
          <li>数据标准管理</li>
          <li>元数据管理</li>
          <li>数据采集</li>
        </ul>
      </div>
      <div class="user">
        <div class="loged-user">
          <el-avatar
            src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"
            style="float: left;"
          ></el-avatar>
          <span class="loged-user-name">Marlon</span>
          <i class="el-icon-caret-bottom" style="float: right;margin: 17px 5px;"></i>
        </div>
        <!-- 悬浮：用户信息 -->
        <ul class="user-info">
          <li>修改密码</li>
          <li>退出</li>
        </ul>
      </div>
    </el-header>
    <el-container class="body">
      <el-main class="main">
          <router-view />
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {};
</script>

<style lang="scss" scoped>
@import "~@/assets/css/platform/gst/config.scss";
.layout {
  width: 100%;
  height: 100%;
  transition: width 0.3s ease;
}
.header {
  box-sizing: border-box;
  overflow: hidden;
  background: $header_bg;
  color: $header_color;
  .logo {
    float: left;
    height: 60px;
    overflow: hidden;
    margin: 0 20px 0 0;
    & > div {
      float: left;
    }
    &-big {
      box-sizing: border-box;
      width: 50px;
      height: 50px;
      border-radius: 5px;
      line-height: 48px;
      border: 1px solid red;
      margin: 4px auto;
      margin-right: 30px;
      text-align: center;
    }
    &-title {
      box-sizing: border-box;
      border: 1px solid red;
      margin: 20px auto;
    }
  }
  .menu {
    float: left;
    overflow: hidden;
    margin: 15px 0px;
    .icon-menu {
      float: left;
      margin: 0 15px;
    }
    &-list {
      float: left;
      overflow: hidden;
      li {
        float: left;
        box-sizing: border-box;
        border: 1px solid red;
        padding: 5px 15px;
        margin: 0 10px;
        cursor: pointer;
      }
    }
  }
  .user {
    float: right;
    width: 150px;
    height: 100%;
    box-sizing: border-box;
    overflow: hidden;
    .loged-user {
      height: 45px;
      margin: 6px 0;
      cursor: pointer;
      &-name {
        display: inline-block;
        position: relative;
        top: -13px;
        left: 10px;
        width: 75px;
        height: 40px;
        line-height: 48px;
        margin: 12px 0px;
        overflow: hidden;
        text-align: center;
      }
    }
    &-info {
      display: none;
      overflow: hidden;
      position: absolute;
      width: 150px;
      li {
        height: 30px;
        line-height: 28px;
        width: 100%;
        box-sizing: border-box;
        border: 1px solid red;
        text-align: center;
        cursor: pointer;
      }
    }
  }
}
.body {
  height: 100%;
  overflow: hidden;
}

.main {
  box-sizing: border-box;
  padding: 0;
}
</style>



